import React, { useState, Suspense } from 'react';
import IngresosAuth from './IngresosAuth';
import { Typography, Paper, Box, CircularProgress } from '@mui/material';

// Component for the Ingresos content after authentication
export const IngresosContent = () => {
    return (
        <div className="min-h-screen bg-gray-50">
            <div className="max-w-7xl mx-auto p-4 sm:p-6 space-y-6">
                <Typography variant="h4" className="text-gray-800 font-bold text-xl sm:text-2xl md:text-3xl">
                    Sección de Ingresos
                </Typography>
                <Paper className="p-4">
                    <Typography variant="body1">
                        Bienvenido a la sección de Ingresos.
                    </Typography>
                    {/* Aquí puedes agregar el contenido real de Ingresos, por ejemplo el componente IngresosTab, etc. */}
                </Paper>
            </div>
        </div>
    );
};

const IngresosPage = () => {
    const [isAuthenticated, setIsAuthenticated] = useState(false);

    if (!isAuthenticated) {
        return <IngresosAuth onAuthenticated={setIsAuthenticated} />;
    }

    return (
        <Suspense fallback={
            <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100vh' }}>
                <CircularProgress size={48} />
            </Box>
        }>
            <IngresosContent />
        </Suspense>
    );
};

export default IngresosPage; 